/* src/styles/about.css */

/* Styles for the entire About component */
.about-container {
    max-width: 600px;
    margin: 0 auto;
    padding: 20px;
    background-color: #f0f0f0;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  }
  
  /* Styles for the heading */
  .about-heading {
    font-size: 2rem;
    margin-bottom: 1rem;
    color: #333;
  }
  
  /* Styles for paragraphs */
  .about-text {
    line-height: 1.6;
    color: #666;
  }
  